body {
  background-color: #151515;
}

#root{
  width: 50%;
  margin: auto;
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas:
    "header header header header"
    "navBar infoDisplay infoDisplay infoDisplay"
    "navBar amount amount amount";
  justify-content: space-evenly;
  justify-items: center;
  align-content: space-evenly;
  align-items: center;
}

.header {
  grid-area: header;
  justify-items: center;
  align-items: center;
  font-size: 2em;
  color: #301b3f;
  background-color: #b4a5a5;
  border-radius: 40px;
  -webkit-border-radius: 40px;
  -moz-border-radius: 40px;
  -ms-border-radius: 40px;
  -o-border-radius: 40px;
  width: 100%;
  text-align: center;
}

.navBar { 
  grid-area: navBar; 
}

.infoDisplay { 
  grid-area: infoDisplay; 
  font-size: 2em;
  font-weight: bolder;
  justify-items: start;
  align-items: center;
  text-align: center;
  color: #b4a5a5;
}

.amount { 
  grid-area: amount; 
  font-size: 1em;
  justify-items: start;
  text-align: center;
  color: #b4a5a5;
}

.navButton {
  width: 175px;
  height: 175px;
  background-color: #423144;
  border: none;
  color: #ffdbc5;
  padding: 32px 32px;
  text-decoration: none;
  margin: 14px 2px;
  cursor: pointer;
  border-radius: 10px;
  text-align: center;
  outline: none;
  box-shadow: 9px 9px #999;
  -webkit-border-radius:;
  -moz-border-radius:;
  -ms-border-radius:;
  -o-border-radius:;
}

.navButton:hover {
  background-color: #3c415c
}

.navButton:active {
  background-color: #3c415c;
  box-shadow: 5px 5px #666;
  transform: translateY(4px);
}

.global-class-name{
  color: #ffdbc5;
}

.butText{
  font-size: medium;
  color: #ffdbc5;
  font-weight: bolder;
}

.warningMessage{
  font-size: .7em;
  color: red;
}

.submitButton{
  background-color: #423144;
  width: 150px;
  border: none;
  color: #ffdbc5;
  padding: 16px 32px;
  text-decoration: none;
  font-weight: bolder;
  font-size: 18px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  box-shadow: 9px 9px #999;
  outline: none;
}

.submitButton:hover {
  background-color: #3c415c;
}

.submitButton:active {
  background-color: #3c415c;
  box-shadow: 5px 5px #666;
  transform: translateY(4px);
}

.clearButton{
  background-color: #423144;
  border: none;
  width: 150px;
  color: #ffdbc5;
  padding: 16px 32px;
  text-decoration: none;
  font-weight: bolder;
  font-size: 18px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 10px;
  box-shadow: 9px 9px #999;
  outline: none;
}

.clearButton:hover {
  background-color: #3c415c;
}

.clearButton:active {
  background-color: #3c415c;
  box-shadow: 5px 5px #666;
  transform: translateY(4px);
}

input[type=number] {
  background-color: #423144;
  color: white;
  width: 350px;
  padding: 12px 20px;
  margin: 8px 0;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 5px 5px #999;
  outline: none;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none; 
    margin: 0; 
}

input[type=number] {
    -moz-appearance:textfield;
}

